.rows-per-view {
  display: flex;
  line-height: 16px;
  font-style: normal;
  align-items: center;
  font-family: var(--font-work-sans-regular);
}
.rows-per-view label,
.rows-per-view span {
  font-size: 14px;
  font-weight: 400;
  color: #545f7d;
  margin-right: 10px;
}
.rows-per-view > div {
  width: 80px;
  height: 30px;
  display: block;
  overflow: hidden;
  margin-right: 10px;
  position: relative;
}
.rows-per-view > div svg {
  top: 2px;
  right: 11px;
  width: 25px;
  height: 25px;
  color: #213f7d;
  position: absolute;
}
.rows-per-view select {
  z-index: 1;
  width: 100%;
  height: 100%;
  border: none;
  font-size: 14px;
  font-weight: 600;
  color: #213f7d;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  padding: 7px 12px;
  position: relative;
  border-radius: 4px;
  background-color: rgba(33, 63, 125, 0.1);
}
.rows-per-view select:focus-visible {
  border: none;
  outline: none;
}/*# sourceMappingURL=RowPerView.css.map */