nav {
  width: 100%;
  height: 1284px;
  max-width: 283px;
  overflow-y: auto;
  position: relative;
  overflow-x: hidden;
  background: #ffffff;
  padding: 39px 47px 0 0;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.04);

  // Switch Organisation style rules
  & > div:first-child > div:first-child {
    width: 100%;
    height: 19px;
    display: flex;
    max-width: 206px;
    margin-left: 30px;
    align-items: center;
    margin-bottom: 52px;
    justify-content: space-between;

    h3 {
      font-size: 16px;
      font-weight: 600;
      color: #213f7d;
      line-height: 19px;
      font-style: normal;
      font-family: var(--font-work-sans-regular);
    }
  }

  // Dashboard style rules
  & > div > div:nth-child(2) {
    width: 100%;
    height: 19px;
    display: flex;
    max-width: 110px;
    margin-left: 30px;
    margin-bottom: 41px;
    align-items: center;
    justify-content: space-between;

    p {
      font-size: 16px;
      font-weight: 400;
      color: #213f7d;
      line-height: 19px;
      font-style: normal;
      font-family: var(--font-work-sans-regular);
    }
  }

  // Category Names
  & > div:first-child > div:nth-child(3) {
    & > div {
      h4 {
        font-size: 12px;
        font-weight: 600;
        color: #545f7d;
        line-height: 14px;
        margin-left: 30px;
        font-style: normal;
        margin-bottom: 20px;
        font-family: var(--font-work-sans-regular);
      }
    }

    ul > li {
      gap: 10px;
      display: flex;
      font-size: 16px;
      list-style: none;
      font-weight: 400;
      color: #213f7d;
      padding-left: 30px;
      line-height: 19px;
      font-style: normal;
      margin-bottom: 31px;
      align-items: center;
      font-family: var(--font-work-sans-regular);
    }

    // Selected User style rule
    & > div:first-child > ul > li:first-child {
      width: 283px;
      height: 40px;
      padding-left: 30px;
      margin-bottom: 20px;
      background: #f3fcfc;
      background: linear-gradient(
        to right,
        #39cdcc 1%,
        #f3fcfc 1%
      );

      font-size: 16px;
      font-weight: 600;
      line-height: 19px;
      font-style: normal;
      font-family: var(--font-work-sans-regular);

      span {
        opacity: 1;
        color: #213f7d;
      }
    }
  }

  .logout {
    width: 100%;
    margin-top: 71px;
    color: #213f7d;
    position: absolute;
    border-top: 1px solid;
    font-family: var(--font-work-sans-regular);

    div {
      gap: 10px;
      display: flex;
      font-size: 16px;
      margin-top: 20px;
      font-weight: 400;
      line-height: 19px;
      font-style: normal;
      padding-left: 30px;
      align-items: center;
    }

    & > p {
      margin-top: 40px;
      padding-left: 30px;
    }
  }

  @media screen and (max-width: 750px) {
    display: none;
    overflow-y: unset;
    overflow-x: unset;
  }
}
