.dashboard {
  width: 100%;
  margin: auto;
  max-width: 1440px;
}
.dashboard > div {
  display: flex;
}
.dashboard > div > div:not(:first-child) {
  width: 100%;
  display: flex;
  padding: 60px;
  overflow: hidden;
  max-width: 1157px;
  flex-direction: column;
  background-color: #fbfbfb;
}
@media screen and (max-width: 950px) {
  .dashboard > div > div:not(:first-child) {
    padding: 60px 30px;
  }
}
@media screen and (max-width: 360px) {
  .dashboard > div > div:not(:first-child) {
    padding: 60px 10px;
  }
}/*# sourceMappingURL=DashboardLayout.css.map */