.phone-nav {
  top: 0;
  left: 0;
  width: 0;
  z-index: 5;
  height: 100%;
  position: fixed;
  transition: 0.5s;
  padding-top: 60px;
  overflow-x: hidden;
  background-color: #fff;

  // CLose Nav button
  & > span {
    top: 12px;
    right: 26px;
    font-size: 50px;
    cursor: pointer;
    font-weight: 600;
    color: #34b2bd;
    position: absolute;
  }

  & > img {
    top: 23px;
    left: 29px;
    width: 170px;
    position: absolute;
  }
}

.phone-nav nav {
  display: block;
  transition: 0.3s;
}

.phone-nav .closebtn {
  top: 0;
  right: 25px;
  font-size: 36px;
  position: absolute;
}

.phone-nav-container {
  top: 0;
  z-index: 1;
  opacity: 0;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  transition: opacity 0.8s ease-in-out;
  background-color: rgba(0, 0, 0, 0.5);
}

.show {
  display: block;
  opacity: 1;
}
